@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: auto;
  background-color: #191923;
  font-family: "Montserrat", sans-serif;
}
svg {
  color: white;
}
.main-header {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.all {
  text-align: center;
}
h1 {
  text-align: center;
  color: #f39237;
  font-size: 5rem;
}
h2,
h3,
h4 {
  text-align: center;
  color: #0e79b2;
  padding: 10px;
}
h1 {
  padding: 10px;
}
h2 {
  padding: 40px;
}
h3 {
  margin-top: 8rem;
}
.btn {
  background-color: #bf1363;
  color: #fbfef9;
  margin: 10px;
}
#arrow-icon {
  margin: 10px;
}
.dropdown {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
#result {
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: white;
  color: black;
  padding: 10px;
}
.converter {
  background-color: #bf1363;
  color: #fbfef9;
  margin: 10px;
  padding: 5px;
}
span {
  color: #9b51e0;
}
img {
  float: left;
}
li {
  height: auto;
  padding: 10px;
  display: -webkit-flex;
  display: flex;
}
.footer {
  text-align: center;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

